.container {
  position: relative;
  height: 100%;
  max-width: 100%;
  display: flex;
  flex: 1;
}
.wrapperProps {
  inset: 0 !important;
}
.shadowTop {
  position: absolute;
  z-index: 50;
  top: 0;
  left: 0;
  right: 0;
  pointer-events: none;
  height: spacing(2);
  background: linear-gradient(
    to bottom,
    rgba(0, 0, 0, 0.3) 0%,
    rgba(0, 0, 0, 0) 100%
  );
}
.shadowBottom {
  z-index: 50;

  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  pointer-events: none;
  height: spacing(2);
  background: linear-gradient(
    to top,
    rgba(0, 0, 0, 0.3) 0%,
    rgba(0, 0, 0, 0) 100%
  );
}
